import Layout from '../components/MyLayout.js'
import Link from 'next/link'

const Spoils = props => (
  <li>
    <Link href={`/spoils?title=${props.title}`}>
      <button>{props.title}</button>
    </Link>
  </li>
)
export default function Spoilers() {
  return (
    <Layout>
      <h1>Spoilers</h1>
      <ul>
        <Spoils title="Hello Next.js" />
        <Spoils title="Learn Next.js is awesome" />
        <Spoils title="Deploy apps with Zeit" />
      </ul>
    </Layout>
  )
}

